import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message, Spin } from 'antd';
const { Content } = Layout;
import SearchForm from './searchForm.jsx'

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '手机号',
                dataIndex: 'mobile'
            }, {
                title: '真实姓名',
                dataIndex: 'name'
            }, {
                title: '性别',
                dataIndex: 'gender',
                render: gender => gender == 0 ? "男" : "女"
            }, {
                title: '公司名称',
                dataIndex: 'companyName'
            }, {
                title: '用户类型',
                dataIndex: 'type',
                render: type => {
                    switch(type) {
                        case "0":
                            return "所有";
                            break;
                        case "1":
                            return "资金方";
                            break;
                        case "2":
                            return "项目方";
                            break;
                    }
                }
            }, {
                title: '注册日期',
                dataIndex: 'registerTime'
            }, {
                title: '状态',
                dataIndex: 'status',
                render: status => {
                    switch(status) {
                        case "0":
                            return "所有";
                            break;
                        case "1":
                            return "已认证";
                            break;
                        case "2":
                            return "待审核";
                            break;
                        case "3":
                            return "未通过";
                            break;
                    }
                }
            }, {
                title: "操作",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.checkAudit.bind(this, data.key)} style={{"marginRight": "20px"}}>审核
                    </Button>
                    <Button onClick={this.checkUserDetail.bind(this, data.key)} style={{"marginRight": "20px"}}>查看
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteUser.bind(this, data.key)} okText="确认" cancelText="取消">
                        <Button style={{marginRight: 20}} type="danger">删除</Button>
                    </Popconfirm>
                    <Popconfirm placement="left" title="是否确认此操作?" onConfirm={this.setForbidden.bind(this, data)} okText="确认" cancelText="取消">
                        <Button type="danger">{data.forbidden ? "解禁" : "禁言"}</Button>
                    </Popconfirm>
                </div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['changePage', 'handleSearch', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentDidMount() {
        this.formRef.handleSubmit(1)
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }

    checkAudit(key) {
        hashHistory.push("/user/manage/audit/" + key);
    }

    checkUserDetail(key) {
        hashHistory.push("/user/manage/detail/" + key);
    }

    setForbidden(selectedData) {
        let that = this;
        ajax({
            url: "user/manage/setForbidden",
            data: {
                id: selectedData.id,
                forbidden: !selectedData.forbidden
            },
            callback: function(data) {
                if(data.status == 200) {
                    message.success("设置成功!", 1);
                    that.state.dataSource.filter(item => {
                        if(item.id == selectedData.id) {
                            item.forbidden = !item.forbidden
                        }
                    });
                    that.setState({dataSource: that.state.dataSource})
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })

    }

    deleteUser(key) {
        let that = this;
        ajax({
            url: "/user/manage/deleteUser",
            method: "post",
            data: {id: key},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != key});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    handleSearch(result) {
        this.setState(
            {
                pagination: {
                    current: result.current,
                    total: result.total
                },
                dataSource: result.list
            }
        )
    }

    changePage(pagination) {
        this.formRef.handleSubmit(pagination.current);
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <div style={{"marginBottom": "30px"}}>
                        <SearchForm searchFuc={this.handleSearch} showLoading={this.showLoading} hideLoading={this.hideLoading} wrappedComponentRef={(inst) => this.formRef = inst} />
                    </div>
                    <Table
                        bordered
                        dataSource={this.state.dataSource}
                        columns={this.state.columns}
                        pagination={this.state.pagination}
                        onChange={this.changePage}
                        loading={this.state.loading}
                    />
                </Content>
            </Layout>
        )
    }
}

module.exports = List;


